<template>
  <div>
    <div class="deptlistheaddiv">
      <span class="deptlistspanhead">{{ this.w3ksname }}</span>
    </div>

    <div style="margin-top:20px">
      <el-tabs tab-position="top" :stretch="true">
        <el-tab-pane
          v-for="item in SchduelDay"
          v-bind:key="item.Id"
          class="el-tab-pane-deptlist"
        >
          <span slot="label" style="font-size:5px">
            <label
              style="display:block;line-height:1;font-size:6px;text-align: center;vertical-align:center"
              >{{ item.WeekInfo }}</label
            >
            {{ item.DayInfo }}
          </span>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div>
      <el-row
        v-for="item in doctorinfos"
        v-bind:key="item.DocCode"
        type="flex"
        align="middle"
        justify="center"
      >
        <el-col :span="4"  :push="1"
          ><el-avatar class="headimg" fit="cover"  :src=item.DocPhotoPath></el-avatar>
        </el-col>
        <el-col :span="12"  :push="1">
          <el-row class="el-row-col-departlist"> {{ item.DocName }}</el-row>
          <el-row class="el-row-col-departlist-font">          
            <label class="el-row-label-departlist-font">{{item.DocDuty}}</label></el-row
          >
        </el-col>
        <el-col :span="8">
          <el-button type="primary" plain @click="confirmreser()"
            >进入预约</el-button
          ></el-col
        >
      </el-row>
    </div>
  </div>
</template>
<script>
import * as commonApi from "../../api/commonfetch";
import { baseUrl } from "../../api/gocenthttpclient";
export default {
  name: "Category",
  data() {
    return {
      SchduelDay:[],
      w3ksid: "",
      w3ksname: "",
      dates: [],
      doctorinfos: [],
      dept2Code:"",
      doctorcode:""
    };
  },
  methods: {
  
    confirmreser() {
      console.log(this.$route);
      this.$router.push({ path: "/shopcart/department/comfirereservsion" });
    }
  },
  created() {

   
  },
  mounted: function() {
     var deptone= this.$route.query.deptone;

      var depttwo= this.$route.query.depttwo;
    const params = {
        orgid: "1",
        dept:deptone,
        dept2:depttwo
        };
     console.log(params)
commonApi.AllDocInfo(params).then(res => {   
          if (res.data.success === true) {
            console.log("111");
               console.log(res.data.data);

                this.doctorinfos=res.data.data
                for (let index = 0; index < this.doctorinfos.length; index++) {
                  this.doctorinfos[index].DocPhotoPath=baseUrl+"/xgtop/"+this.doctorinfos[index].DocName+".jpg";
                }

          } else {
      
          }
        })
        .catch(err => {
          console.log(err);
        });


         const params2 = {
        dept2:depttwo
        };
commonApi.AllSchedual(params2).then(res => {  
          if (res.data.success === true) {
               console.log(res.data);
               var array = [];
               for (let i = 0; i < res.data.data.length; i++) {
  
                    if(array.indexOf(res.data.data[i].Day)>-1)
                     {
                          continue;               
                     }
             array.push(res.data.data[i].Day)
               }
             
             for (let index = 0; index < array.length; index++) {            
                 var weekstr = this.common.getweek(array[index]);
                var daystr=this.common.formatDate(new Date(array[index]),'MM-dd')
                this.SchduelDay.push({Id:index,DayInfo:daystr,WeekInfo:weekstr})
               
             }

              console.log(this.SchduelDay)
          } else {
            
          }
        })
        .catch(err => {
          console.log(err);
        });
  }
};
</script>
<style scoped>
@import "../../assets/css/gshawn.css";
</style>



